<template>
  <za-page-content scroll="true">
    <za-table ref="table" :data="accounts" :total="pageConfig.total" @selection-change="handleSelectionChange" @action-change="changePage" :isLoading="loadingTable">
      <div style="padding-bottom: 0px;" slot="searchBar">
        <el-form :model="filters" ref="filtersForm">
          <el-form-item>
            <el-col :span="8">
              <el-col :span="6">组件ID:</el-col>
              <el-col :span="14">
                <el-input v-model="filters.id" :clearable="true" placeholder="组件ID"></el-input>
              </el-col>
            </el-col>
            <el-col :span="8">
              <el-col :span="6" :offset="1">组件名称:</el-col>
              <el-col :span="14">
                <el-input v-model="filters.name" :clearable="true" placeholder="组件名称"></el-input>
              </el-col>
            </el-col>
            <el-col :span="8">
              <el-col :span="6" :offset="2">状态:</el-col>
              <el-col :span="14">
                <el-select style="width:100%" v-model="filters.status" placeholder="请选择状态">
                  <el-option label="生效" value="01"></el-option>
                  <el-option label="失效" value="02"></el-option>
                </el-select>
              </el-col>
            </el-col>
          </el-form-item>
          <el-form-item>
            <el-col :span="16">
              <el-col :span="3">创建时间:</el-col>
              <el-date-picker v-model="filters.timeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" start-placeholder="开始时间" end-placeholder="结束时间">
              </el-date-picker>
            </el-col>
            <el-col :span='8' align="center">
              <el-button icon="el-icon-search" type="primary" @click="filter">查询</el-button>
              <el-button type="success" @click="createUnit">新建组件</el-button>
            </el-col>
          </el-form-item>
        </el-form>
      </div>

      <el-table-column prop="id" label="组件ID" width="80"></el-table-column>
      <el-table-column prop="name" label="组件类型">
        <template slot-scope="scope">
          {{scope.row.type === '01' ? 'PK组件' : '其他组件'}}
        </template>
      </el-table-column>
      <el-table-column prop="name" label="组件名称"></el-table-column>
      <el-table-column prop="gmtCreated" label="创建时间" :formatter="formatterDate" width="180"></el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          {{scope.row.status === '01' ? '生效' : '失效'}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width="240">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEditUnit(scope.row)">编辑</el-button>
					<!-- 取消上下架功能 -->
          <!-- <el-button v-if="scope.row.status==='02'" size="mini" type="success" @click="batchOnlineOrOffLine('01', scope.$index, scope.row)">上架</el-button>
          <el-button v-else size="mini" type="danger" @click="batchOnlineOrOffLine('02', scope.$index, scope.row)">下架</el-button> -->
          <el-button size="mini" type="danger" @click="handleDelUnit(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </za-table>

    <!-- 新增 & 编辑 -->
    <EditUnit ref="editUnit" @submitUnit="initData"></EditUnit>
  </za-page-content>
</template>

<script>
import EditUnit from "./components/EditUnit.vue";
import formatterDate from "../../filters/date/index.js";
import { ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";

export default {
  components: { EditUnit },
  data() {
    return {
      showdalog: false,
      filters: {
        id: "",
        name: "",
        type: "",
        status: ""
      },
      accounts: [],
      pageConfig: {
        pageNum: 1,
        pageSize: 10,
        total: 1
      },
      formValidate: {
        type: [{ required: true, message: "请选择所属栏目", trigger: "blur" }],
        nickName: [{ required: true, message: "请输入昵称", trigger: "blur" }],
        status: [{ required: true, message: "请选择状态", trigger: "blur" }],
        level: [{ required: true, message: "请选择等级", trigger: "blur" }]
      }
    };
  },

  methods: {
    filter() {
      const gmtCreatedStart = !!this.filters.timeRange
        ? this.filters.timeRange[0]
        : "";
      const gmtCreatedEnd = !!this.filters.timeRange
        ? this.filters.timeRange[1]
        : "";
      this.pageConfig = {
        ...this.pageConfig,
        ...this.filters,
        gmtCreatedStart,
        gmtCreatedEnd,
        pageNum: 1
      };
      if (!this.$refs.table.resetCurrentPage()) {
        this.initData();
      }
    },

    initData() {
      this.loadingTable = true;
      ajaxPostCommon(ajaxUrl.getUnitList, {
        ...this.pageConfig
      }).then(res => {
        setTimeout(() => {
          this.accounts = res.result.list;
          this.pageConfig.total = res.result.total;
          this.loadingTable = false;
        }, 200);
      });
    },

    createUnit() {
      this.$refs.editUnit.show();
    },

    batchOnlineOrOffLine(status, index, row) {
      ajaxPostCommon(ajaxUrl.onlineOrOfLine, { id: row.id, status }).then(
        res => {
          let messageConfig = {};
          if (res.returnCode === "200") {
            messageConfig = {
              showClose: true,
              message: "成功",
              type: "success"
            };
          } else {
            messageConfig = {
              showClose: true,
              message: res.returnMessage,
              type: "error"
            };
          }
          this.$message(messageConfig);
          this.initData();
        }
      );
    },

    handleEditUnit(row) {
      this.$refs.editUnit.show(row);
    },

    handleDelUnit(id) {
      this.$confirm("确定删除该组件?", "提示", {}).then(() => {
        ajaxPostCommon(ajaxUrl.delUnit, { id }).then(res => {
          this.initData();
          this.$message({
            showClose: true,
            message: "删除成功",
            type: "success"
          });
        });
      });
    },

    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },

    formatterDate(r, c, v) {
      return formatterDate(v);
    },

    changePage(params) {
      let pageParams = {
        pageNum: params.page,
        pageSize: params.rows || this.pageConfig.pageSize
      };
      this.pageConfig = { ...this.pageConfig, ...pageParams };
      this.initData();
    }
  },

  created() {
    this.initData();
  }
};
</script>

<style lang="scss" scoped>
</style>




