<template>
  <div class="edit-unit-dialog">
    <el-dialog :title="dialogTitle" :visible.sync="showDialog" @close="hide" width="680px" top="5vh">
      <el-form :model="unitData" ref="form" :rules="formValidate" label-position="left" label-width="110px">
        <el-row :gutter="20">
          <el-col :span="23">
            <span class="title-tip">注: 最多40个字</span>
            <el-form-item label="组件名称:" prop="name">
              <el-input v-model="unitData.name" auto-complete="off" placeholder="请输入组件名称"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="组件类型:" prop="type">
              <el-select v-model="unitData.type" placeholder="请选择组件类型">
                <el-option label="PK组件" value="01"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
					<!-- 取消上下架功能 -->
          <!-- <el-col :span="11">
            <el-form-item label="组件状态:">
              <el-select v-model="unitData.status" placeholder="请选择组件状态">
                <el-option label="上架" value="01"></el-option>
                <el-option label="下架" value="02"></el-option>
              </el-select>
            </el-form-item>
          </el-col> -->
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="开启积分奖励:" prop="isOpenReward">
              <el-select :disabled="dialogTitle==='编辑组件'" v-model="unitData.isOpenReward" placeholder="是否开启积分奖励">
                <el-option label="是" value="01"></el-option>
                <el-option label="否" value="02"></el-option>
              </el-select>
            </el-form-item>
          </el-col>

					<el-col :span="11" v-if="unitData.isOpenReward==='01'">
						<el-form-item label="投票所需积分:">
              <el-input :disabled="dialogTitle==='编辑组件'" type="number" v-model="unitData.costPoint"></el-input>
            </el-form-item>
					</el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="23">
            <el-form-item label="有效期:" prop="timeRange">
              <el-date-picker v-model="unitData.timeRange" style="width: 100%" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" start-placeholder="开始时间" end-placeholder="结束时间">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="活动配图:" prop="coverImage" style="width: 100%">
              <div class="img-tip" style="color: red">* 建议宽高比690:180</div>
              <el-upload class="avatar-uploader" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :show-file-list="false" :on-success="handleAvatarSuccess">
                <img v-if="unitData.coverImage" :src="unitData.coverImage" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="24">
          <el-col :span="12">
            <el-form-item label="选项A:" prop="selectA">
              <el-input v-model="unitData.selectA"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="初始票数:" prop="initA">
              <el-input type="number" v-model="unitData.initA"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="24">
          <el-col :span="12">
            <el-form-item label="选项B:" prop="selectB">
              <el-input v-model="unitData.selectB"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="初始票数:" prop="initB">
              <el-input type="number" v-model="unitData.initB"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer" align="center">
        <el-button @click="showDialog = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { ajaxPostCommon, ajaxUploadCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";
import moment from "moment";

export default {
  data() {
    return {
      ajaxUrl,
      dialogTitle: "新建组件",
      showDialog: false,
			unitData: {},
			formValidate: {
				name: {
          required: true,
          message: "请输入组件名称",
          trigger: "blur"
        },
        type: {
          required: true,
          message: "请选择组件类型",
          trigger: "blur"
				},
				isOpenReward: {
					required: true,
          message: "请选择是否开启积分奖励",
          trigger: "blur"
				},
				timeRange: {
					required: true,
          message: "请选择有效期",
          trigger: "blur"
				},
				coverImage: {
					required: true,
          message: "请上传活动配图",
          trigger: "blur"
				},
				selectA: {
					required: true,
          message: "请填写选项A",
          trigger: "blur"
				},
				selectB: {
					required: true,
          message: "请填写选项B",
          trigger: "blur"
				},
				initA: {
					required: true,
          message: "请填写初始票数",
          trigger: "blur"
				},
				initB: {
					required: true,
          message: "请填写初始票数",
          trigger: "blur"
				},
			}
    };
  },

  methods: {
    show(data) {
      this.unitData = this.parseData(data) || {coverImage: ''};
      this.showDialog = true;
    },
    parseData(data) {
      if (!data) {
        // 新建
        this.dialogTitle = "新建组件";
        return false;
      }
      this.dialogTitle = "编辑组件";
      const forMatter = "YYYY-MM-DD HH:mm:ss";
      const timeRange = [
        moment(data.gmtValidityStart).format(forMatter),
        moment(data.gmtValidityEnd).format(forMatter)
      ];
      return { ...data, ...JSON.parse(data.extraInfo), timeRange };
    },
    hide() {
      this.$refs.form.clearValidate();
			this.showDialog = false;
    },
    handleAvatarSuccess(res) {
      this.unitData.coverImage = res.result;
		},
		validate() {
			let validatePass = false;
			this.$refs.form.validate(pass => {
        if (pass) validatePass = true;
      });
      return validatePass;
		},
    submit() {
			if (!this.validate()) return false;

      let unitData = this.unitData;
      unitData.extraInfo = JSON.stringify({
        selectA: unitData.selectA,
        initA: unitData.initA,
        selectB: unitData.selectB,
        initB: unitData.initB
      });
      unitData.gmtValidityStart = unitData.timeRange[0];
      unitData.gmtValidityEnd = unitData.timeRange[1];
      ajaxPostCommon(ajaxUrl.saveOrUpdateUnit, unitData).then(res => {
        if (res.returnCode === "200") {
          this.$emit("submitUnit");
          this.hide();
          this.$message({
            showClose: true,
            message: '保存成功',
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: res.returnMsg,
            type: "error"
          });
        }
      });
    }
  }
};
</script>

<style lang="scss">
  .title-tip {
    color:  red;
  }
</style>





